import React, { useState } from "react";

import { Container } from "./index.style.js";

import Navbar from "./Navbar.jsx";

import CCCMarketPlaceMobile from "../../../img/play2EarnIMG.png";
import CCCMarketPlaceDesktop from "../../../img/play2EarnDesktopIMG.png";
import FarmDesktop from "../../../img/farmDesktopIMG.png"
import FarmMobile from "../../../img/farmIMG.png"
import GovernanceDesktop from "../../../img/governanceDesktopIMG.png"
import GovernanceMobile from "../../../img/governanceIMG.jpeg"
import UtilityDesktop from "../../../img/utilityDesktopIMG.png"
import UtilityMobile from "../../../img/utilityIMG.png"

import Content from "./Content.jsx";

const AboutCCC = () => {
  const [conteudo, setConteudo] = useState("about");

  const handleClick = (content) => {
    setConteudo(content);
  };

  return (
    <Container>
      <Navbar handleClick={handleClick} />

      {conteudo === "about" && (
        <Content
          DesktopIMG={UtilityDesktop}
          title="About"
          text=" Welcome to Clash Cards Champions! Our goal is to provide our users with the best trading card experience using NFTs and Play2Earn. The cards are based on Binance Smart Chain blockchain and can be collected as NFTs (Non-Fungible Tokens)."
        />
      )}
      {conteudo === "marketplace" && (
        <Content
          DesktopIMG={CCCMarketPlaceDesktop}
          title="CCC Marketplace"
          text=" A platform where users can buy NFT card packs and adventure portions. On the same platform users can exchange their cards with other users, and payment is made through stablecoin $BUSD in both markets.
          "
        />
      )}
      {conteudo === "battle" && (
        <Content
          DesktopIMG={GovernanceDesktop}
          title="Battle Center"
          text=" The Battle Center consists of two modes, Free Mode and Ultra Mode."
        />
      )}
      {conteudo === "farm" && (
        <Content
          DesktopIMG={FarmDesktop}
          title="CCC-Farm"
          text="Users can stake their $CCC to increase their yield on the platform's native token, participate in the platform's governance system, and even participate in NFT card airdrops."
        />
      )}
    </Container>
  );
};

export default AboutCCC;


